.cost,
.name {
    z-index: 1;
}

.card-background {
    position: absolute;
    height: 600px;
    width: 450px;
    margin: 18px 18px 0 18px;
    border-radius: 10px 10px 42px 8%;
    background-color: #bbb;
    z-index: 0;
}

.card-container {
    border: 1px solid black;
    width: 488px;
    height: 680px;
    /*margin: 56px auto 0;*/
    border-radius: 25px;
    box-sizing: border-box;
    /*box-shadow: -8px 9px 16px -3px gray;*/
    background: #171314;
}

.card-frame {
    z-index: 1;
    display: flex;
    flex-direction: column;
}

.frame-art {
    position: absolute;
    left: 36px;
    top: 78px;
    height: 298px;
    /*width - left - right;*/
    width: calc(488px - 36px - 39px);
}

.frame-text-box {
    position: absolute;
    left: 34px;
    top: 426px;
    height: 203px;
    width: calc(488px - 35px - 38px + 2px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 50px 6px;
    box-sizing: border-box;
    font-size: 1.2em;
    line-height: 1.2em;
}

.new-instruction {
    display: block;
    margin-top: 8px;
    margin-bottom: 8px;
}

.frame-loyalty,
.frame-stats,
.frame-inner-stats,
.frame-header,
.frame-type-line {
    height: 35px;
    line-height: 35px;
